/**
 * @fileOverview AI-Powered Quote Generator.
 *
 * - generateQuote - A function that handles the quote generation process.
 * - GenerateQuoteInput - The input type for the generateQuote function.
 * - GenerateQuoteOutput - The return type for the generateQuote function.
 */

import {ai} from '@/ai/genkit';
import {z} from 'genkit';

const GenerateQuoteInputSchema = z.object({
  requestDetails: z
    .string()
    .describe('Detailed description of the client request for digital services, including the type of service, specific requirements, and any relevant background information.'),
});
export type GenerateQuoteInput = z.infer<typeof GenerateQuoteInputSchema>;

const GenerateQuoteOutputSchema = z.object({
  projectScope: z
    .string()
    .describe('A detailed project scope outlining the deliverables, milestones, and timelines.'),
  costEstimate: z
    .string()
    .describe('An estimated cost range for the project, considering the scope and complexity.'),
  additionalNotes: z
    .string()
    .describe('Any additional notes or considerations for the quote, such as potential risks or assumptions.'),
});
export type GenerateQuoteOutput = z.infer<typeof GenerateQuoteOutputSchema>;

export async function generateQuote(input: GenerateQuoteInput): Promise<GenerateQuoteOutput> {
  return generateQuoteFlow(input);
}

const prompt = ai.definePrompt({
  name: 'generateQuotePrompt',
  input: {schema: GenerateQuoteInputSchema},
  output: {schema: GenerateQuoteOutputSchema},
  prompt: `You are an AI-powered quote generator for digital services. Analyze the client's request and provide a detailed project scope and cost estimate.

Client Request Details: {{{requestDetails}}}

Based on the request details, generate a project scope, cost estimate, and any additional notes.

Project Scope:
Cost Estimate:
Additional Notes:`, // Ensure the prompt guides the LLM to fill each output field
});

const generateQuoteFlow = ai.defineFlow(
  {
    name: 'generateQuoteFlow',
    inputSchema: GenerateQuoteInputSchema,
    outputSchema: GenerateQuoteOutputSchema,
  },
  async input => {
    const {output} = await prompt(input);
    return output!;
  }
);
