
"use server";

import { generateQuote, type GenerateQuoteInput, type GenerateQuoteOutput } from "@/ai/flows/generate-quote";
import { clientInquiries, services, testimonials, type ClientInquiryStatus } from "@/lib/placeholder-data";
import type { Service, Testimonial } from "@/lib/placeholder-data";
import { revalidatePath } from "next/cache";
import { redirect } from "next/navigation";
import type { ServiceFormValues, TestimonialFormValues } from "@/lib/schemas";

interface GenerateQuoteActionInput extends GenerateQuoteInput {
  inquiryId: string;
}

// AI Quote Generation
export async function generateQuoteAction(input: GenerateQuoteActionInput): Promise<GenerateQuoteOutput> {
  try {
    const result = await generateQuote({ requestDetails: input.requestDetails });
    
    const inquiryToUpdate = clientInquiries.find(inq => inq.id === input.inquiryId);
    if (inquiryToUpdate) {
      inquiryToUpdate.generatedQuote = result;
    } else {
      console.warn(`generateQuoteAction: Inquiry with ID ${input.inquiryId} not found.`);
    }

    revalidatePath("/admin/inquiries");
    revalidatePath("/admin");
    return result;
  } catch (error) {
    console.error("Error in generateQuoteAction:", error);
    if (error instanceof Error) {
        throw new Error(`AI Quote Generation Failed: ${error.message}`);
    }
    throw new Error("An unexpected error occurred during AI quote generation.");
  }
}

// Service Actions
export async function createServiceAction(values: ServiceFormValues) {
  console.log("Creating new service (simulated in-memory)", values);
  const newService: Service = {
    id: `service-${Date.now()}-${Math.random().toString(36).substring(2, 7)}`,
    ...values,
  };
  services.push(newService);
  
  revalidatePath("/admin/services");
  revalidatePath("/services"); // Public services page
  revalidatePath("/"); // Homepage
  revalidatePath("/admin"); // Dashboard
  redirect("/admin/services");
}

export async function updateServiceAction(id: string, values: ServiceFormValues) {
  console.log(`Updating service ${id} (simulated in-memory)`, values);
  const serviceIndex = services.findIndex(s => s.id === id);
  if (serviceIndex !== -1) {
    services[serviceIndex] = { ...services[serviceIndex], ...values };
  } else {
    console.warn(`updateServiceAction: Service with ID ${id} not found.`);
  }

  revalidatePath("/admin/services");
  revalidatePath(`/admin/services/edit/${id}`);
  revalidatePath("/services"); // Public services page
  revalidatePath("/"); // Homepage
  revalidatePath("/admin"); // Dashboard
  redirect("/admin/services");
}

export async function deleteServiceAction(id: string) {
  console.log(`Deleting service ${id} (simulated in-memory)`);
  const serviceIndex = services.findIndex(s => s.id === id);
  if (serviceIndex !== -1) {
    services.splice(serviceIndex, 1);
  } else {
    console.warn(`deleteServiceAction: Service with ID ${id} not found.`);
  }
  
  revalidatePath("/admin/services");
  revalidatePath("/services"); // Public services page
  revalidatePath("/"); // Homepage
  revalidatePath("/admin"); // Dashboard
  // No redirect, let the page refresh
}


