// Inquiry Actions
export async function updateInquiryStatusAction(id: string, status: ClientInquiryStatus) {
  console.log(`Updating inquiry ${id} to status ${status} (simulated in-memory)`);
  const inquiry = clientInquiries.find(inq => inq.id === id);
  if (inquiry) {
    inquiry.status = status;
  } else {
    console.warn(`updateInquiryStatusAction: Inquiry with ID ${id} not found.`);
  }
  revalidatePath("/admin/inquiries");
  revalidatePath("/admin"); // Dashboard
}
